/* Text styles */
.src-comp-sidebar-sidebar {
  padding: 0 30px;
  border-right: 1px solid var(--border);
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.src-comp-sidebar-block {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}
.src-comp-sidebar-block .src-comp-sidebar-firstTitle {
  font-weight: 500;
  margin-bottom: 10px;
}
.src-comp-sidebar-block .src-comp-sidebar-secondTitle {
  color: var(--blue-base);
  transition: color 0.2s;
  cursor: pointer;
  padding-left: 10px;
}
.src-comp-sidebar-block .src-comp-sidebar-secondTitle:hover {
  color: var(--blue-hover);
}