/* Text styles */
.src-pages-submit-wrapper {
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch;
  padding: 30px;
}

.src-pages-submit-form {
  width: 100%;
  display: grid;
  grid-row-gap: 32px;
  grid-column-gap: 32px;
  grid-template-columns: 1fr 1fr 1fr;
  margin-bottom: 30px;
  min-width: 0;
}
.src-pages-submit-form > * {
  min-width: 0;
}

.src-pages-submit-result {
  margin-bottom: 20px;
}