/* Text styles */
.src-comp-header-header {
  background-color: var(--background-primary);
  border-bottom: 1px solid var(--border);
  display: flex;
  justify-content: center;
  width: 100%;
}
.src-comp-header-header .src-comp-header-container {
  max-width: 1440px;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  display: flex;
  height: 72px;
  flex-shrink: 0;
}

.src-comp-header-logo {
  padding-left: 24px;
  font-weight: 500;
}

.src-comp-header-githubButton {
  margin-left: auto;
  flex-shrink: 0;
}