/* Text styles */
.src-pages-install-examples-inputWrapper {
  display: flex;
  flex-flow: column nowrap;
  align-items: flex-start;
  position: relative;
  width: 100%;
}
.src-pages-install-examples-inputWrapper label {
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
  user-select: none;
  color: var(--content-3);
  padding-bottom: 8px;
  cursor: pointer;
}
.src-pages-install-examples-inputWrapper .src-pages-install-examples-inputInner {
  width: 100%;
  position: relative;
  border: 1px solid var(--border);
  border-radius: 8px;
  transition: color 0.2s ease, border-color 0.2s ease, background 0.2s ease;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  overflow: hidden;
  background: var(--background-primary);
}
.src-pages-install-examples-inputWrapper .src-pages-install-examples-inputInner input[type=text],
.src-pages-install-examples-inputWrapper .src-pages-install-examples-inputInner textarea {
  font-size: 16px;
  display: block;
  min-height: 40px;
  width: 100%;
  background: transparent;
  border: 0;
  padding: 0 16px;
  color: var(--content-1);
}
.src-pages-install-examples-inputWrapper .src-pages-install-examples-inputInner input[type=text]::placeholder,
.src-pages-install-examples-inputWrapper .src-pages-install-examples-inputInner textarea::placeholder {
  color: var(--content-3);
}
.src-pages-install-examples-inputWrapper textarea {
  min-height: 120px;
  resize: none;
  padding-top: 12px;
  padding-bottom: 12px;
}
.src-pages-install-examples-inputWrapper.src-pages-install-examples-focused .src-pages-install-examples-inputInner, .src-pages-install-examples-inputWrapper:hover .src-pages-install-examples-inputInner {
  border-color: var(--content-3);
}
.src-pages-install-examples-inputWrapper.src-pages-install-examples-hasErrors .src-pages-install-examples-inputInner {
  border-color: var(--red-base) !important;
}
.src-pages-install-examples-inputWrapper .src-pages-install-examples-errors {
  font-size: 14px;
  color: var(--red-base);
  width: 100%;
  padding-top: 8px;
}
.src-pages-install-examples-inputWrapper .src-pages-install-examples-errors .src-pages-install-examples-errorItem {
  padding-bottom: 5px;
}
.src-pages-install-examples-inputWrapper .src-pages-install-examples-errors .src-pages-install-examples-errorItem:last-child {
  padding-bottom: 0;
}
.src-pages-install-examples-inputWrapper.src-pages-install-examples-disabled {
  pointer-events: none;
  user-select: none;
}
.src-pages-install-examples-inputWrapper.src-pages-install-examples-disabled .src-pages-install-examples-inputInner {
  background: var(--background-secondary);
}