/* Text styles */
.src-comp-example-example {
  border: 1px solid var(--border);
  border-radius: 6px;
  background: var(--background-primary);
  display: flex;
  flex-flow: column nowrap;
  margin-bottom: 20px;
}
.src-comp-example-example .src-comp-example-exampleBlock {
  flex-shrink: 0;
  padding: 24px;
  padding-bottom: 0;
}
.src-comp-example-example .src-comp-example-exampleBottom {
  flex-shrink: 0;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  padding: 10px 24px;
}
.src-comp-example-example .src-comp-example-exampleBottom .src-comp-example-description {
  flex-grow: 1;
  font-size: 14px;
  color: var(--content-3);
  padding-right: 20px;
}
.src-comp-example-example .src-comp-example-exampleBottom .src-comp-example-codeButton {
  margin-left: auto;
  flex-shrink: 0;
}
.src-comp-example-example .src-comp-example-code {
  border-top: 1px solid var(--border);
  flex-shrink: 0;
  padding: 24px;
}
.src-comp-example-example .src-comp-example-code .mantine-CodeHighlightTabs-root {
  margin-bottom: 0 !important;
  background: var(--background-secondary) !important;
}